"use client"

import { useEffect, useState } from "react"
import { useRouter } from "next/navigation"
import { Button } from "@/components/ui/button"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table"

interface FileData {
  name: string
  path: string
}

export default function FilesPage() {
  const router = useRouter()
  const [files, setFiles] = useState<FileData[]>([])
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState<string | null>(null)

  useEffect(() => {
    const fetchFiles = async () => {
      try {
        // For demo purposes, we'll just use the sample file
        // In a real app, you'd fetch this from an API endpoint
        setFiles([
          {
            name: "07-03-2025.csv",
            path: "/files/07-03-2025.csv"
          }
        ])
        setLoading(false)
      } catch (err) {
        setError('Failed to load files')
        setLoading(false)
      }
    }

    fetchFiles()
  }, [])

  const handleTrigger = async (fileName: string) => {
    try {
      const response = await fetch('/api/trigger', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ fileName }),
      })

      const data = await response.json()

      if (!response.ok) {
        throw new Error(data.error || 'Failed to trigger process')
      }

      // You might want to show a success message here
    } catch (err) {
      setError(err instanceof Error ? err.message : 'Failed to trigger process')
    }
  }

  if (loading) {
    return (
      <div className="container mx-auto p-4">
        <Card>
          <CardContent className="p-6">
            Loading...
          </CardContent>
        </Card>
      </div>
    )
  }

  return (
    <div className="container mx-auto p-4">
      <Card>
        <CardHeader>
          <CardTitle className="text-2xl font-bold">CSV Files</CardTitle>
        </CardHeader>
        <CardContent>
          {error && (
            <div className="text-red-500 mb-4">{error}</div>
          )}
          <Table>
            <TableHeader>
              <TableRow>
                <TableHead>File Name</TableHead>
                <TableHead className="text-right">Actions</TableHead>
              </TableRow>
            </TableHeader>
            <TableBody>
              {files.map((file) => (
                <TableRow key={file.name}>
                  <TableCell>{file.name}</TableCell>
                  <TableCell className="text-right">
                    <div className="flex justify-end gap-2">
                      <Button
                        variant="outline"
                        onClick={() => router.push(`/files/${encodeURIComponent(file.name)}`)}
                      >
                        View Data
                      </Button>
                      <Button
                        onClick={() => handleTrigger(file.name)}
                      >
                        Trigger API
                      </Button>
                    </div>
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
          <div className="mt-4">
            <Button
              variant="outline"
              onClick={() => router.push('/upload')}
            >
              Upload New File
            </Button>
          </div>
        </CardContent>
      </Card>
    </div>
  )
}
